/* teacher code */

.teacher {
    transition: transform 0.3s ease-in-out;
    position: relative; /* Add position relative to the parent */
}

.teacher-name {
    display: none; /* Hide the teacher's name initially */
    position: absolute; /* Position it relative to the parent */
    top: 50%; /* Center vertically */
    left: 42%; /* Center horizontally */
    transform: translate(-50%, -50%); /* Center it precisely */
    color: #fff; /* Text color */
    padding: 5px 10px; /* Add some padding for better styling */
    border-radius: 5px; /* Add rounded corners */
    font-size: 25px;
}

.teacher:hover .teacher-name {
    display: block;
    color: white;
    z-index: 1; /* Show the teacher's name on hover */
}

.teacher:hover {
    filter:  brightness(0.5) sepia(1) hue-rotate(10deg) saturate(10) contrast(1) drop-shadow(0px 0px 5px #7e1162);
    transform: scale(1);
}

